body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    background-color: #000000;
    color: #ffffff;
}

.container {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    width: 930px;
    height: auto;
}

.subtitle {
    display: flex;
    margin-top: 80px;
    margin-bottom: 20px;
}

.subtitle__element {
    background-color: #6532F8;
    font-size: 15px;
    font-weight: 400;

    padding: 10px 23px;
    text-transform: uppercase;

    border-radius: 40px;
}

.title {
    font-size: 120px;
    font-weight: 400;
    margin-bottom: 35px;
}

.form__body {
    background-color: #232323;
    width: 930px;
    height: auto;
    border-radius: 20px;
}

.search__films {
    border: none;
    background: inherit;
    font-size: 30px;
    color: #ffffff;
    padding-bottom: 20px;

    border-bottom: 1px solid #F3F6F9;

    margin-top: 83px;
    margin-left: 86px;
    margin-right: 20px;
    margin-bottom: 30px;
    outline:none;
}

.search__btn {
    border: none;
    background: #6532F8;
    border-radius: 50%;
    padding: 15px;
    cursor: pointer;
}

.spisoc__films {
    width: 100%;
    height: 400px;
    overflow: scroll;
}

.film {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    background: #3A3939;
    color: #F3F6F9;
    width: 790px;
    height: 71px;
    margin-bottom: 5px;
    border-radius: 20px;

    padding-left: 50px;
    position: relative;

    cursor: pointer;
    transition: all .3s;
}
.film:hover {
    opacity: 0.6;
}

.crestic {
    margin-right: 30px;
    cursor: pointer;

    position: absolute;
    right: 0;
}

.circle {
    width: 19px;
    height: 18px;
    border-radius: 50%;
    background: inherit;
    border: 1px solid #F3F6F9;

    margin-right: 20px;
}


.ready {
    text-decoration: line-through;
    opacity: 0.4;

}

.ready .circle {
    background: #6532F8;
}